// src/components/QRCodeGenerator.js
import React from 'react';
import { QRCodeCanvas } from 'qrcode.react';

const QRCodeGenerator = ({ queueId, width = 150, height = 150 }) => {
  // Use different base URLs based on environment
  let baseUrl;
  if (process.env.NODE_ENV === 'development') {
    // In development, use the origin (e.g., http://localhost:3000)
    baseUrl = window.location.origin;
  } else {
    // In production, use PUBLIC_URL set in package.json homepage (e.g., https://mohammadyaslam.github.io/smartq-react)
    baseUrl = process.env.PUBLIC_URL || window.location.origin;
  }
  
  // Remove any trailing slash to avoid double slashes in the URL
  baseUrl = baseUrl.replace(/\/+$/, "");
  
  // Construct the QR code URL using HashRouter routing (so that join-queue is recognized)
  const qrText = `${baseUrl}/#/join-queue?queueId=${queueId}`;
  
  return <QRCodeCanvas value={qrText} size={width} />;
};

export default QRCodeGenerator;
